.App {
  text-align: center;
}

.App-logo {
  position: absolute;
  top: 0;
  height: 40vmin;
  pointer-events: none;
  animation: show-skinner 0.5s ease-in;
  transition: all ease-in 0.5s;
}

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #ff5757;
}

@keyframes show-skinner {
  0% {
    opacity: 0;
  }
  50% {
    margin-left: 1rem;
  }
  75% {
    margin-left: 0;
    margin-right: 1rem;
  }
  100% {
    margin-right: 0;
    opacity: 1;
  }
}